/*---
title: Composition
section: Utilities
---*/

:root {
  --fontFamily--sans: 'Source Sans Pro', sans-serif;
  --fontFamily--monospaced: Consolas, "Andale Mono WT", "Andale Mono", "Lucida Console", "Lucida Sans Typewriter", "DejaVu Sans Mono", "Bitstream Vera Sans Mono", "Liberation Mono", "Nimbus Mono L", Monaco, "Courier New", Courier, monospace;
  --fontFamily-page: var(--fontFamily--sans);
  --fontSize--tiny: .75rem; /* 12px */
  --fontSize--small: .875rem; /* 14px */
  --fontSize--semiNormal: 1rem; /* 16px */
  --fontSize--normal: 1.125rem; /* 18px */
  --fontSize--normalPlus: 1.25rem; /* 20px */
  --fontSize--semiMedium: 1.5rem; /* 24px */
  --fontSize--medium: 2rem; /* 32px */
  --fontSize--semiLarge: 2.5rem; /* 40px */
  --fontSize--large: 3rem; /* 48px */
  --fontSize--xlarge: 4.7rem; /* 75.2px */
  --fontSize-page: 100%;
  --fontSize-heading: var(--fontSize--normalPlus);
  --fontWeight-page: 400;
  --fontWeight-page--bold: 800;
  --lineHeight-page: 1;
  --lineHeight-paragraph: 1.4;
  --lineHeight-heading: 1.3;
  --no-bullet: {
    list-style-image: none;
    list-style-type: none;
    margin-left: 0;
  };
  --list-horizontal: {
    border: 0;
    clear: both;
    margin: 0;
    padding: 0;
  };
  --ellipsis: {
    display: inline-block;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    width: 100%;
  };
  --break-word: {
    -ms-word-break: break-all; /* Safari can wait */
    word-break: break-word;
    word-wrap: break-word;
  };
}

.u-font-size--tiny { font-size: var(--fontSize--tiny); }
.u-font-size--small { font-size: var(--fontSize--small); }
.u-font-size--semi-normal { font-size: var(--fontSize--semiNormal); }
.u-font-size--normal { font-size: var(--fontSize--normal); }
.u-font-size--normal-plus { font-size: var(--fontSize--normalPlus); }
.u-font-size--semi-medium { font-size: var(--fontSize--semiMedium); }
.u-font-size--medium { font-size: var(--fontSize--medium); }
.u-font-size--semi-large { font-size: var(--fontSize--semiLarge); }
.u-font-size--large { font-size: var(--fontSize--large); }
.u-font-size--xlarge { font-size: var(--fontSize--xlarge); }
.u-no-bullet { @apply --no-bullet; }
.u-no-bullets {
  list-style: none;
  &__item { @apply --no-bullet; };
}
.u-list-horizontal { @apply --list-horizontal; }
.u-ellipsis { @apply --ellipsis; }
.u-break-word { @apply --break-word; }

.is-positive {
  color: var(--color-positive) !important;

  .c-icon { color: currentColor !important; }
}

.is-informative { color: var(--color-informative) !important; }
.is-neutral { color: var(--color-neutral) !important; }
.is-live { color: var(--color-live) !important; }
.is-alterable { color: var(--color-alterable) !important; }
.is-negative { color: var(--color-negative) !important; }