/*---
title: Listing
section: Objects
---*/

/* Listing - Basic */

:root {
  --backgroundColor-listing--active: var(--white);
  --backgroundColor-listing__action-group: var(--white);
  --backgroundColor-listing__action-group--hover: var(--grey-100);
  --borderColor-listing: var(--grey-600);
  --color-listing__action: var(--grey-400);
  --color-listing__action--hover: var(--grey-700);
  --color-listing__action--active: var(--color-page);
}

.o-listing {
  @apply --row;
  border-bottom: 1px solid var(--borderColor-listing);
  position: relative;
  transition: background-color .3s ease-in-out, box-shadow .3s ease-in-out;
  &:first-child {
    border-top: 1px solid var(--borderColor-listing);
  }

  &:hover,
  &.is-active {
    background-color: var(--backgroundColor-listing--active);
    box-shadow: var(--shadow-1);

    .o-listing__action-group {
      background-color: var(--backgroundColor-listing__action-group--hover);
      border-bottom-left-radius: var(--global-rounded);
    }

    .o-listing__actions .c-icon {
      color: var(--color-listing__action--hover);
    }
  }
}


.o-listing__status {
  cursor: pointer;
  padding: .5em .5rem;
  width: 40px;
}


.o-listing__info {
  @apply --width-column-10;
  @media (--md-up) {
    @apply --width-column-auto;
  }
}


.o-listing__wrapper {
  @apply --top;
  @apply --row;
  cursor: pointer;
  min-height: 48px;
  padding: .25em 0;
}


.o-listing__general {
  @apply --width-column-auto;
  @apply --start;
  @media (--md-up) {
    @apply --width-column-4;
  }
}


.o-listing__name {
  display: inline-block;
  font-weight: bold;
  margin-bottom: 0 !important;
  margin-top: 0 !important;
}


.o-listing__duration {
  text-transform: uppercase;

  @media (--md-up) {
    @apply --end;
    @apply --width-column-4;
  }
}


.o-listing__actions {
  padding-right: 0;
  position: absolute;
  right: 0;
  top: 0;

  &.is-active { @apply --fade-in; }

  .c-icon {
    background-color: transparent;
    color: var(--color-listing__action);
    cursor: pointer;
    transition: color .1s ease-in-out;

    &:--active {
      color: var(--grey-300);
      border-radius: 50px;
    }
  }
}


.o-listing__action-group {
  background-color: var(--backgroundColor-listing__action-group);
  display: inline-block;
  padding: 4px;
  transition: background-color var(--global-duration) ease;

  & > .c-button--icon {
    display: none;
    @media(--md-up) {
      display: inherit;
    }
  }

  @media (--sm-down) {
    .o-listing__action {
      display: none;
    }
  }
}


.o-listing__menu-group {
  @apply --end;
  background-color: var(--white);
  font-size: var(--fontSize--semiNormal);
  margin-right: .5rem;
  min-width: 130px;
  @media (--md-up) { @apply --fade-out; }

  &.is-active { @apply --fade-in; }

  .o-listing__action {
    display: block;
    padding: 5px;
    text-align: left;
    width: 100%;
  }
}


.o-listing__stats {
  @apply --center;
  @apply --width-column-4;
  margin: .5em 0;

  .o-label:nth-child(odd) { font-weight: bold; }
}


.o-listing__delivery { @apply --width-column-2; }





/* -- Listing - Detail */

.o-listing__info--detail {
  .o-listing__heading {
    border-bottom: 1px solid var(--grey-600);
    display: inline-block;
    font-size: var(--fontSize--tiny);
    font-weight: bold;
    text-transform: uppercase;
    width: 100%;
    & + ul {
      font-size: var(--fontSize--small);
      margin-bottom: .75em;
    }
  }
}


/* TODO Identify where this is used then get rid of it */
.u-column--icons { padding-left: 0 !important; }
