/*---
title: Loader
section: Components
---*/

:root {
  --size-loader-secondary: 2em;
}

.c-loader {
  animation: 3s infinite alternate spin;
	position: relative;
  transform: rotate(20deg);
  width: 30px;

  &::before {
    color: var(--color-loader);
  }

  &::after {
    color: var(--color-loader--alt);
    margin-top: 2px;
    transform: rotate(180deg);
  }

  &::before,
  &::after {
    @apply --triangle;
    @apply --triangle-top;
    content: '';
    border-bottom-width: 2em;
  }
}

.c-loader-secondary {
  animation: 1.15s cubic-bezier(0.215, 0.61, 0.355, 1) 0s infinite ripple;
  border: 0 solid var(--color-loader-secondary);
  border-radius: 50%;
  width: auto;

  &::before,
  &::after { content: initial; }
}

@keyframes spin {
  0% { transform: scale(1) rotate(0deg); }
	50% { transform: scale(1) rotate(80deg); }
	100% { transform: scale(1) rotate(-160deg); }
}

@keyframes ripple {
  0% {
    height: 0;
    width: 0;
  }
  30% {
    border-width: calc(var(--size-loader-secondary) / 1.9);
    height: var(--size-loader-secondary);
    opacity: 1;
    width: var(--size-loader-secondary);
  }
  100% {
    border-width: 0;
    height: var(--size-loader-secondary);
    opacity: 0;
    width: var(--size-loader-secondary);
  }
}

.c-loader-secondary--neutral {
  border-color: var(--color-neutral);
}