/*---
title: Card
section: Components
---*/

:root {
  --padding-card: .75em 1rem;
  --margin-card: 0 0 1em;
  --card: {
    background-color: var(--white);
    box-shadow: var(--shadow-1);
    color: var(--color-page);
    display: block;
    margin: var(--margin-card);
    min-width: 100px;
    padding: var(--padding-card);
    position: relative;
    transition: box-shadow var(--global-duration) ease;
    z-index: 1;
  };
  --card-wide: {
    background-color: var(--white);
    box-shadow: var(--shadow-1);
    color: var(--color-page);
    margin: var(--margin-card);
    overflow: hidden;
    padding: var(--padding-card);
    position: relative;
    z-index: 1;
  };
  --card-reset: {
    background: transparent !important;
    box-shadow: none !important;
  };
}


.c-card {
  @apply --card;


  &.is-active {
    box-shadow: var(--shadow-3) !important;
    z-index: 2;
  }


  &.is-inactive {
    border: 1px solid var(--borderColor-inactive);
    box-shadow: none;
  }


  & .c-menu:first-child {
    margin-top: -.75em !important;
  }


  .t-dark & {
    background-color: var(--grey-800);
    color: var(--white);
  }
}


  .c-card__title {
    @apply --row;
    @apply --middle;
    background-color: var(--grey-100);
    margin: -.5em -1rem .5em;
    min-height: 38px;
    padding: .5em 1rem;

    .is-inactive & { background-color: var(--white); }

    .t-dark & {
      background-color: var(--grey-600);
    }
  }


  .c-card__heading {
    display: inline-block;
    font-weight: bold;
    margin: 0;
  }


  .c-card__actions {
    @apply --fade-out;
    color: var(--grey-700);

    :--active & { @apply --fade-in; }

    .c-icon {
      cursor: pointer;
      margin-left: .75em;
    }
  }


.c-card--row {
  @apply --middle;
  margin-bottom: 0;
  margin-left: -.5rem;
  margin-right: -.5rem;
  position: relative;
  transition: background .1s ease-in-out, box-shadow .1s ease-in-out;

  &:not(:last-child) {
    border-bottom: 1px solid rgba(0, 0, 0, .1);
  }

  @media (--md-up) {
    margin-left: inherit;
    margin-right: inherit;
  }

  &:--active {

    .u-row {
      @apply --middle;
      &:not(:only-child) {
        margin: 0 0 .75em;
      }
      &:not(:first-child) {
        border-top: 1px solid var(--grey-100);
        margin-bottom: 0;
        padding-top: .75em;
      }
    }
  }

  &:focus .c-card__actions { @apply --fade-in; }

  @media (--md-up) {
    &:hover .c-card__actions { @apply --fade-in; }
  }
}


.c-card--transparent {
  @apply --card-reset;
}


.c-card--ruled {
  @apply --card-reset;
  border-bottom: 1px solid var(--borderColor-inactive);
  margin: 0;

  &:not(:last-child) { border-top: 1px solid var(--borderColor-inactive); }

  &:last-child { margin-bottom: 1em; }

  :--headings { text-transform: uppercase; }
}


.c-card--round { border-radius: var(--global-rounded); }


.c-card--zone {
  background-color: var(--blue-50);
  border: 1px dashed var(--blue-500);
  border-radius: var(--global-rounded);
  box-shadow: none;
  color: var(--blue-500);
  display: block;
  font-size: var(--fontSize--normal);
  padding: 1em 1rem;
  text-align: center;
  text-transform: uppercase;
}


.c-card--small { padding: 0; }
